    q: "What is bodhicitta?",
    a: "Bodhicitta is the 'mind of awakening'  the aspiration to attain complete buddhahood for the benefit of all sentient beings. It has two aspects: aspiring bodhicitta (the wish) and engaging bodhicitta (the practice). Ultimate bodhicitta is the direct realization of emptiness. Bodhicitta is what distinguishes the Mahyna path from other Buddhist paths.",
