const glossary = [
  { term: "rya", definition: "A 'noble one'  a being who has directly perceived emptiness (from the 1st bhmi onward)." },
  { term: "Bhmi", definition: "Literally 'ground' or 'stage'  one of the ten levels of the bodhisattva path." },
  { term: "Bodhicitta", definition: "The mind of awakening  the aspiration to attain buddhahood for all beings." },
  { term: "Bodhisattva", definition: "A being who has generated bodhicitta and is on the path to buddhahood." },
  { term: "Dharmakya", definition: "The 'truth body' of a buddha  the ultimate dimension of enlightenment." },
  { term: "Dhyna", definition: "Meditative absorption  a state of deep, focused concentration." },
  { term: "Klea", definition: "Mental affliction  any mental state that disturbs peace of mind and drives sasra." },
  { term: "Mahyna", definition: "The 'Great Vehicle'  the Buddhist tradition focused on the bodhisattva path to full buddhahood." },
  { term: "Nirva", definition: "The cessation of suffering  liberation from sasra." },
  { term: "Pramit", definition: "Literally 'gone to the other shore'  a transcendent perfection practiced by bodhisattvas." },
  { term: "Praj", definition: "Wisdom  particularly the wisdom that directly perceives emptiness (nyat)." },
  { term: "Prattyasamutpda", definition: "Dependent origination  the principle that all phenomena arise in dependence on causes and conditions." },
  { term: "Samdhi", definition: "Meditative concentration  single-pointed absorption on an object." },
  { term: "Sasra", definition: "The cycle of birth, death, and rebirth driven by karma and afflictions." },
  { term: "amatha", definition: "Calm abiding  meditative practice that develops single-pointed concentration." },
  { term: "nyat", definition: "Emptiness  the absence of inherent, independent existence in all phenomena." },
  { term: "Trikya", definition: "The three bodies of a buddha: dharmakya, sabhogakya, and nirmakya." },
  { term: "Upya", definition: "Skillful means  the ability to adapt teachings to suit different beings." },
  { term: "Vipayan", definition: "Insight meditation  analytical meditation that penetrates the nature of reality." },
];

export default function ReferencesPage() {
  const [openFaq, setOpenFaq] = useState(null);
  const [glossaryFilter, setGlossaryFilter] = useState("");

  const filteredGlossary = glossary.filter(
    (g) =>
      g.term.toLowerCase().includes(glossaryFilter.toLowerCase()) ||
      g.definition.toLowerCase().includes(glossaryFilter.toLowerCase())
  );

  return (
    <div className="references-page">
      <header className="page-hero">
        <h1>References</h1>
        <p className="hero-subtitle">
          Frequently asked questions, glossary, and guidance for navigating
          the teachings
        </p>
      </header>

      <section className="references-section">
        <h2>Frequently Asked Questions</h2>
        <div className="faq-list">
          {faqs.map((faq, i) => (
            <div key={i} className="faq-item">
              <button
                className={`faq-question ${openFaq === i ? "open" : ""}`}
                onClick={() => setOpenFaq(openFaq === i ? null : i)}
              >
                <span>{faq.q}</span>
                <span className="faq-toggle">{openFaq === i ? "" : "+"}</span>
              </button>
              {openFaq === i && <div className="faq-answer">{faq.a}</div>}
            </div>
          ))}
        </div>
      </section>

      <section className="references-section">
        <h2>Sanskrit Glossary</h2>
        <input
          type="text"
          className="glossary-search"
          placeholder="Search glossary..."
          value={glossaryFilter}
          onChange={(e) => setGlossaryFilter(e.target.value)}
        />
        <div className="glossary-list">
          {filteredGlossary.map((g) => (
            <div key={g.term} className="glossary-item">
              <dt>{g.term}</dt>
              <dd>{g.definition}</dd>
            </div>
          ))}
        </div>
      </section>

      <section className="references-section">
        <h2>Study Path Suggestions</h2>
        <div className="study-paths">
          <div className="study-path-card">
            <h3>Beginner Path</h3>
            <ol>
              <li>Four Noble Truths</li>
              <li>Noble Eightfold Path</li>
              <li>Three Marks of Existence</li>
              <li>Five Aggregates</li>
              <li>Twelve Links of Dependent Origination</li>
            </ol>
          </div>
          <div className="study-path-card">
            <h3>Meditation Path</h3>
            <ol>
              <li>Nine Stages of amatha</li>
              <li>Four Dhynas</li>
              <li>Four Formless Absorptions</li>
              <li>Four Immeasurables</li>
            </ol>
          </div>
          <div className="study-path-card">
            <h3>Bodhisattva Path</h3>
            <ol>
              <li>Bodhicitta</li>
              <li>Four Immeasurables</li>
              <li>Six Pramits</li>
              <li>Five Paths</li>
              <li>Ten Bhmis</li>
              <li>Three Bodies of a Buddha</li>
            </ol>
          </div>
        </div>
      </section>
    </div>
  );
}
